import React, { useState, Component } from "react";
import { Link } from "gatsby";
import { IoIosArrowRoundForward } from "react-icons/io";
import Sidebar from "sections/Blog-sidebar";
import { ChakraProvider, Flex, Heading, useMediaQuery, createBreakpoints, Button, Box, SimpleGrid, Text, Select, Input, Center, Divider, Image, extendTheme } from "@chakra-ui/react"
import logo from "assets/images/app/logo.png"
import { yellow } from "@mui/material/colors";


class PosCal extends React.Component {

  constructor(props) {
    super(props);

    this.state = {

      enteredValues: {
        accountBalance: props.accountBalance,
        riskperecentage: props.riskperecentage,
        pips: props.pips,
      }
    }
  }


  changeText = (position_size) => {

    this.setState({ position_size });
  };

  changeText2 = (amount_risk) => {

    this.setState({ amount_risk });
  };

  handleFirstNameChanged(event) {
    var enteredValues = this.state.enteredValues;
    enteredValues.accountBalance = event.target.value;
    this.setState({ enteredValues: enteredValues });


  }

  handlerisk(event) {
    var enteredValues = this.state.enteredValues;


    enteredValues.riskperecentage = event.target.value;

    this.setState({ enteredValues: enteredValues });
  }



  handlespips(event) {
    var enteredValues = this.state.enteredValues;
    enteredValues.pips = event.target.value;
    this.setState({ enteredValues: enteredValues });
  }

  handleButtonClicked() {
    const account_balance = parseInt(this.state.enteredValues.accountBalance)
    const risk_perecentage = parseInt(this.state.enteredValues.riskperecentage)
    const stop_loss = parseInt(this.state.enteredValues.pips)

    this.setState({ test: "Updated Content!" });

    const positionSize = account_balance * risk_perecentage / stop_loss
    const amountAtRisk = account_balance * (risk_perecentage / 100)

    console.log("Position Size " + positionSize);

    this.changeText(positionSize)
    this.changeText2(amountAtRisk)

  }

  render() {


    const { position_size } = this.state
    const { amount_risk } = this.state


    return (

      <ChakraProvider>

        <Box width="100%" height="auto" bg="#0b0640" paddingBottom={10} >

          <Image src={logo} alt="" width={{ base: "100px", md: "150px", lg: "200px" }} objectFit="cover" />

          <Box height="auto" marginBottom={0} margin={100} bg="transparent" w="auto" marginTop={0} >

            <Text color="white" paddingTop={5} fontWeight="bold" fontSize={{ base: "24px", md: "40px", lg: "56px" }} align="center" border={3}>  Position Size Calculator </Text>

            <Box bg="#003060" w={{ base: "250px", md: "auto", lg: "auto" }} p={4} color="white" height="auto" marginTop={10} marginBottom="10%" marginX={{ base: "0%", md: "25%", lg: "15%" }} borderRadius={25} border="none" box-shadow="md" >



              <SimpleGrid columns={{ base: 1, md: 3, lg: 3 }} spacing={0} padding={5}   >

                <Box bg="transparent" height="350px" margin="0">

                  <form method="post">

                    <Text fontSize={{ base: "15px", md: "15px", lg: "22px" }} >Account Currency </Text>


                    <Select bgColor="white" color="black" placeholder="Select" height={{ base: 8, md: 35, lg: 35 }} borderRadius={25} width={{ base: "100%", md: "50%", lg: "50%" }} border="none" marginBottom={5} marginTop={5} fontSize={{ base: "15px", md: "20px", lg: "20px" }} >
                      <option value="AUD">USD</option>

                    </Select>

                    <Text fontSize={{ base: "15px", md: "15px", lg: "22px" }}   >Account Balance </Text>

                    <Input color="black" width={{ base: "100%", md: "50%", lg: "50%" }} bgColor="white" padding={4} height={{ base: 8, md: 35, lg: 35 }} placeholder="0" value={this.state.enteredValues.accountBalance} onChange={this.handleFirstNameChanged.bind(this)} type="text" marginTop={5} borderRadius={25} border="none" marginBottom={5} fontSize="18" />

                    <Text fontSize={{ base: "15px", md: "15px", lg: "22px" }} marginBottom={5} > Currency Pair </Text>

                    <Select height={{ base: 8, md: 35, lg: 35 }} color="black" bgColor="white" placeholder="Currency Pair" borderRadius={25} width={{ base: "100%", md: "50%", lg: "50%" }} border="none" fontSize={{ base: "15px", md: "20px", lg: "20px" }} >
                      <option value="GBP/USD">AUD/USD</option>
                      <option value="USD/CHF">EUR/USD</option>
                      <option value="USD/CAD">GBP/USD</option>
                      <option value="USD/CAD">USD/CHF</option>
                      <option value="USD/CAD">USD/JPY</option>
                      <option value="USD/CAD">USD/CAD</option>
                    </Select>
                  </form>

                </Box>

                <Box bg="transparent" height="350px" margin="0">

                  <form method="post">

                    <Text fontSize={{ base: "15px", md: "15px", lg: "22px" }} paddingBottom={5} >Risk Precentage </Text>

                    <Input bgColor="white" color="black" padding={4} height={{ base: 8, md: 35, lg: 35 }} type="number" value={this.state.enteredValues.riskperecentage} onChange={this.handlerisk.bind(this)} placeholder="0" width={{ base: "100%", md: "50%", lg: "50%" }} borderRadius={25} border="none" marginBottom={5} fontSize={18} />

                    <Text fontSize={{ base: "15px", md: "15px", lg: "22px" }} paddingBottom={5} >Stop Loss (pips) </Text>

                    <Input padding={4} height={{ base: 8, md: 35, lg: 35 }} width={{ base: "100%", md: "50%", lg: "50%" }} bgColor="white" color="black" type='number' value={this.state.enteredValues.pips} onChange={this.handlespips.bind(this)} placeholder="0" borderRadius={25} border="none" marginBottom={14} fontSize={18} />

                    <Text fontSize="2"></Text>

                    <Button borderRadius={25} borderColor="darkblue" borderWidth={1} width={{ base: "100%", md: "50%", lg: "50%" }} height={{ base: 8, md: 35, lg: 38 }} bg="#0b0640" fontSize={18} marginTop={18} onClick={this.handleButtonClicked.bind(this)}  >Calculate</Button>

                  </form>
                </Box>

                <Box borderRadius={20} margin={0} bg="#023e72" height="auto" boxShadow="md" p="0" width="auto" >

                  <Text align="center" padding={0} alignSelf="center" fontWeight="bold" fontSize={{ base: "18px", md: "20px", lg: "25px" }} marginBottom={5} marginTop={2} > Results </Text>

                  <Text paddingLeft="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0} >Amount At Risk </Text>

                  <Text align="right" marginRight="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0}>{amount_risk}</Text>

                  <Divider marginBottom={25} marginInlineStart="10%" marginTop={0} bgColor="white" height={0.4} width="85%" />



                  <Text paddingLeft="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0} >Position Size ( Units ) </Text>

                  <Text align="right" marginRight="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0}>{position_size}</Text>

                  <Divider marginBottom={25} marginInlineStart="10%" marginTop={0} bgColor="white" height={0.4} width="85%" />

                  <Text paddingLeft="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0} >Standard Lots </Text>

                  <Text align="right" marginRight="10%" marginX="70%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0}></Text>

                  <Divider marginBottom={25} marginInlineStart="10%" marginTop={0} bgColor="white" height={0.4} width="85%" />

                  <Text paddingLeft="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0} >Mini Lots </Text>

                  <Text align="right" marginRight="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0}></Text>

                  <Divider marginBottom={25} marginInlineStart="10%" marginTop={0} bgColor="white" height={0.4} width="85%" />

                  <Text paddingLeft="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0} >Micro Lots </Text>

                  <Text align="right" marginRight="10%" fontSize={{ base: "15px", md: "18px", lg: "18px" }} marginBottom={0}></Text>

                  <Divider marginBottom={25} marginInlineStart="10%" marginTop={0} bgColor="white" height={0.4} width="85%" />


                </Box>
              </SimpleGrid>
            </Box>
          </Box>
        </Box>

      </ChakraProvider>
    );
  }
}

export default PosCal








